import { useNavigate } from 'react-router-dom';
import {message} from 'antd'
import {RegisterApi} from 'utils/Api'
import { Card, Form, Input, Button } from 'antd';

const Register = () => {
	const navigate = useNavigate()

  const onFinish = (value) => {
      if (value.comfirmPasswd === value.passwd) {
        RegisterApi(value).then((res) => {
					localStorage.setItem('__admin_token', res.data.token)
					navigate('/admin')
        });
      } else {
        message.error('两次密码不一致');
      }
  };



  return (

    <Card title="后台管理系统-注册" bordered={false} style={{ width: 500, margin: '8rem auto', textAlign: 'center' }}>
      <Form
        name="basic"
				labelCol={{span: 4}}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        autoComplete="off"
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[{ required: true, message: '请输入用户名!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="密码"
          name="passwd"
          rules={[{ required: true, message: '请输入密码' }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label="确认密码"
          name="comfirmPasswd"
          rules={[{ required: true, message: '请再次输入密码' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label="安全码"
          name="secretCode"
          rules={[{ required: true, message: '请输入用户名!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item wrapperCol={{  span: 16 }}>
          <Button type="primary" htmlType="submit">
            注册
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default Register;
